* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.body-index {
  background: #bdc3c7;
  background: -webkit-linear-gradient( to right, #2c3e50, #bdc3c7 );
  background: linear-gradient(to right, #2c3e50, #bdc3c7);
}

.body-switcher {
  background: #2193b0;
  background: -webkit-linear-gradient( to right, #6dd5ed, #2193b0 );
  background: linear-gradient(to right, #6dd5ed, #2193b0);
}

.body-timer {
  background: #74ebd5;
  background: -webkit-linear-gradient( to right, #acb6e5, #74ebd5 );
  background: linear-gradient(to right, #acb6e5, #74ebd5);
}

.body-promises {
  background: #1c92d2;
  background: -webkit-linear-gradient( to right, #f2fcfe, #1c92d2 );
  background: linear-gradient(to right, #f2fcfe, #1c92d2);
}

img {
  max-width: 100%;
  display: block;
}

ul {
  justify-content: center;
  align-items: center;
  list-style: none;
  display: flex;
}

a {
  color: #000;
  max-width: 300px;
  background-color: rgba(255, 255, 255, .1);
  border: solid #000;
  border-radius: 10px;
  align-items: center;
  margin: 10px;
  padding: 5px 10px;
  font-size: large;
  text-decoration: none;
  display: inline-flex;
}

a:hover {
  color: #fff;
  background-color: #000;
  border: solid #fff;
}

.list-btn {
  justify-content: center;
  align-items: center;
  display: flex;
}

.btn-white {
  background-color: #fff;
}

.custom-btn {
  width: 130px;
  height: 40px;
  color: #000;
  cursor: pointer;
  background: none;
  border-radius: 50px;
  margin: 20px;
  padding: 10px 25px;
  font-family: Lato, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: inline-block;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), inset -7px -7px 10px rgba(0, 0, 0, .1), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.timer-wrap {
  flex-direction: row;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.picker {
  height: 50px;
  text-align: center;
  cursor: pointer;
  background-color: #ffc685;
  border: 2px solid #000;
  border-radius: 8px;
  outline: none;
  margin-bottom: 100px;
  margin-left: 10px;
  font-size: large;
  font-weight: 700;
}

.picker:disabled {
  background-color: #60e5de;
  border: 2px solid #000;
  outline: none;
}

.timer {
  flex-direction: row;
  justify-content: center;
  gap: 30px;
  display: flex;
}

.field {
  text-transform: uppercase;
  width: 140px;
  height: 140px;
  background-color: #fff352;
  border: 2px solid #000;
  border-radius: 50%;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  padding-top: 15px;
  font-size: x-large;
  font-weight: 700;
  display: flex;
}

.value {
  color: #071ddd;
  font-size: 50px;
  font-weight: 700;
}

.value.end {
  color: #21ebff;
}

.label {
  color: #000;
  font-size: 15px;
  font-weight: 700;
}

.btn-start {
  width: 140px;
  height: 50px;
  color: #000;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #fff352;
  border: 2px solid #000;
  border-radius: 8px;
  margin: 0 20px;
  font-size: large;
  font-weight: 700;
  box-shadow: 5px 5px 1px 1px rgba(76, 86, 106, .2);
}

.btn-start:disabled {
  color: #000;
  box-shadow: none;
  background-color: #58f5f7;
  border: 2px solid #fff352;
  margin: 0 20px;
}

.btn-reload {
  width: 180px;
  height: 50px;
  cursor: pointer;
  background-color: #fff352;
  border: 2px solid #000;
  border-radius: 8px;
  font-size: large;
  font-weight: 700;
}

.form {
  width: 400px;
  background-color: rgba(0, 0, 0, .6);
  border: 3px solid #000;
  border-radius: 4px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  padding: 12px;
  display: flex;
}

.form label {
  color: #fff;
  border-radius: 4px;
  flex-direction: column;
  margin-bottom: 16px;
  font-size: large;
  font-weight: 700;
  display: flex;
}

.form button {
  font: inherit;
  cursor: pointer;
  border: 2px solid #000;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: large;
  font-weight: 700;
}

.form input {
  border: 2px #000;
  border-radius: 4px;
  outline: none;
  padding: 8px 12px;
}

